  display: none;
}

/*************** mixins ***************/
/* Media queries */
/* remove list format */
/*************** header ***************/
.header {
  margin: 0 1.7rem 0 1rem;
}
@media (min-width: 768px) {
  .header {
    margin: 0 5.6rem 0 5.2rem;
  }
}
@media (min-width: 992px) {
  .header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 0 5.6rem 0 5.2rem;
  }
}
.header span {
  cursor: pointer;
}

.left-section {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: space-between;
}
.left-section .edie-title {
  font-style: normal;
  font-weight: bolder;
  font-size: 2.4rem;
  line-height: 3.5rem;
  color: #333333;
}
@media (min-width: 992px) {
  .left-section .edie-title {
    font-size: 3.6rem;
    line-height: 5.3rem;
  }
}
@media (min-width: 992px) {
  .left-section #menu {
    display: none;
  }
}

.nav {
  display: none;
}
@media (min-width: 992px) {
  .nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
.nav a {
  text-decoration: none;
  color: #000000;
  text-align: center;
  font-size: 2rem;
  line-height: 2.6rem;
}
.nav a:first-child {
  padding-top: 1.2rem;
}
.nav a:last-child {
  padding-bottom: 1.2rem;
}
@media (min-width: 992px) {
  .nav a {
    font-size: 2.4rem;
    line-height: 3.6rem;
  }
  .nav a:not(:first-child) {
    margin: 0 0 0 2rem;
  }
  .nav a:first-child {
    padding-top: 0;
  }
  .nav a:last-child {
    padding-bottom: 0;
  }
}

.mobile-menu {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #f2f2f2;
}
.mobile-menu a {
  margin-bottom: 2rem;
}

/*************** footer ***************/
footer {
  background-color: #100e1d;
  display: flex;
  flex-direction: column;
}
footer .footer-container {
  margin: 3.6rem 3.2rem 19.5rem 3.2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  footer .footer-container {
    margin: 3.6rem 3.2rem 11.8rem;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-around;
  }
}
footer .footer-container ul {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 0;
}
footer .footer-container ul li {
  width: fit-content;
}
footer .footer-container ul li a {
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 3.6rem;
  color: #ffffff;
}
footer .footer-container .edie-social {
  display: flex;
  flex-direction: column;
  margin-top: 6.4rem;
  margin-bottom: 6.5rem;
}
@media (min-width: 768px) {
  footer .footer-container .edie-social {
    margin-top: 0;
    margin-bottom: 0;
  }
}
footer .footer-container .edie-social h2 {
  font-family: "Heebo", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 3.6rem;
  line-height: 5.3rem;
  color: #f2f2f2;
}
footer .footer-container .edie-social ul {
  margin-top: 3rem;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
}
footer .footer-container .edie-social ul li:nth-child(2) {
  margin-left: 1rem;
  margin-right: 1rem;
}
footer .footer-container form p {
  color: #f2f2f2;
}
footer .footer-container form .email-submit {
  display: flex;
  justify-content: space-between;
  background: #f2f2f2;
  border-radius: 1.2rem;
}
footer .footer-container form .email-submit input[type=email] {
  border: 0;
  background-color: transparent;
  padding: 1.2rem 0 1.2rem 1.2rem;
}
footer .footer-container form .email-submit input[type=email]:focus {
  outline: none;
}
footer .footer-container form .email-submit input[type=email]::placeholder {
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 2.1rem;
  color: #bdbdbd;
}
footer .footer-container form .email-submit button {
  border: 0;
  padding: 0 2rem 0 2rem;
  font-style: normal;
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 2.1rem;
  color: #f2f2f2;
  background: #2d9cdb;
  border-radius: 1.2rem;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}
footer .footer-text {
  margin: 0 3rem 2.4rem 3rem;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 1.7rem;
  text-align: center;
  color: #a9a9a9;
}
footer .footer-text .username {
  text-decoration: underline;
  font-weight: bold;
}

/*************** section one ***************/
.section-one {
  margin: 0 1.7rem 0 1rem;
}
@media (min-width: 768px) {
  .section-one {
    margin: 0 5.6rem 0 5.2rem;
  }
}
.section-one #block-01 {
  padding: 2rem 4.6rem 1.5rem 3rem;
}
@media (min-width: 768px) {
  .section-one #block-01 {
    padding: 2rem 33rem 1.5rem 5rem;
  }
}
@media (min-width: 992px) {
  .section-one #block-01 {
    padding: 2rem 25rem 6rem 8rem;
  }
}
@media (min-width: 1200px) {
  .section-one #block-01 {
    padding: 2rem 40rem 6rem 20rem;
  }
}
@media (min-width: 1440px) {
  .section-one #block-01 {
    padding: 2rem 60rem 6rem 20rem;
  }
}
.section-one #block-01 h2 {
  font-weight: bold;
  font-size: 2.4rem;
  line-height: 3.6rem;
  color: #333333;
}
@media (min-width: 992px) {
  .section-one #block-01 h2 {
    font-size: 4.8rem;
