}
@media (min-width: 768px) {
  .section-one #block-02 {
    padding: 2rem 35rem 1.5rem 5rem;
  }
}
@media (min-width: 992px) {
  .section-one #block-02 {
    padding: 6rem 35rem 1.5rem 8rem;
  }
}
@media (min-width: 1200px) {
  .section-one #block-02 {
    padding: 6rem 57rem 1.5rem 20rem;
  }
}
@media (min-width: 1440px) {
  .section-one #block-02 {
    padding: 6rem 73rem 1.5rem 20rem;
  }
}
.section-one #block-02 h2 {
  margin-right: 4.6rem;
  font-weight: bold;
  font-size: 2.4rem;
  line-height: 3.6rem;
  color: #333333;
}
@media (min-width: 992px) {
  .section-one #block-02 h2 {
    font-size: 4.8rem;
    line-height: 7.2rem;
  }
}
.section-one #block-02 p {
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.5rem;
  color: #828282;
}
@media (min-width: 768px) {
  .section-one #block-02 p {
    padding-right: 2rem;
  }
}
@media (min-width: 992px) {
  .section-one #block-02 p {
    font-size: 1.8rem;
    line-height: 2.7rem;
    padding-right: 2rem;
  }
}
@media (min-width: 1440px) {
  .section-one #block-02 p {
    padding-right: 2rem;
  }
}
.section-one #block-02 .form .email-submit {
  display: flex;
  justify-content: space-between;
  background: #f2f2f2;
  border-radius: 1.2rem;
}
.section-one #block-02 .form .email-submit input[type=email] {
  border: 0;
  background-color: transparent;
  padding: 1.2rem 0 1.2rem 1.2rem;
}
.section-one #block-02 .form .email-submit input[type=email]:focus {
  outline: none;
}
.section-one #block-02 .form .email-submit input[type=email]::placeholder {
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 2.1rem;
  color: #bdbdbd;
}
.section-one #block-02 .form .email-submit button {
  border: 0;
  padding: 0 2rem 0 2rem;
  font-style: normal;
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 2.1rem;
  color: #f2f2f2;
  background: #2d9cdb;
