/*************** section two ***************/
.section-two {
  margin: 6rem 2rem;
}
@media (min-width: 992px) {
  .section-two {
    margin: 10rem 12rem;
  }
}
@media (min-width: 1200px) {
  .section-two {
    margin: 12rem 14rem;
  }
}
.section-two h2 {
  width: 26.52rem;
  font-weight: bold;
  font-size: 2.4rem;
  line-height: 3.6rem;
  color: #333333;
  margin-left: 2rem;
  margin-right: 5rem;
}
@media (min-width: 768px) {
  .section-two h2 {
    width: 24rem;
  }
}
@media (min-width: 992px) {
  .section-two h2 {
    font-size: 3.6rem;
    line-height: 5.4rem;
    width: 32rem;
  }
}
.section-two .services {
  margin: 2rem 0;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .section-two .services {
    flex-direction: row;
    align-items: baseline;
  }
}
.section-two .services .card {
  display: flex;
  flex-direction: column;
  border: none;
  border-radius: 2.4rem;
  row-gap: 1rem;
  padding: 5rem 4rem;
  box-shadow: 0px 10px 30px rgba(51, 51, 51, 0.1);
  border-radius: 2.4rem;
}
@media (min-width: 768px) {
  .section-two .services .card {
    padding: 4rem 2rem;
  }
}
@media (min-width: 992px) {
  .section-two .services .card {
    box-shadow: none;
    padding: 4rem 2rem;
  }
}
@media (min-width: 1200px) {
  .section-two .services .card {
    padding: 4rem 6rem;
  }
}
.section-two .services .card .card-icon {
  width: fit-content;
  padding: 2.05rem;
  border-radius: 1.4rem;
  border: none;
}
.section-two .services .card .card-icon span {
  color: #ffffff;
}
.section-two .services .card #card-icon-01 {
  background-color: #2d9cdb;
}
.section-two .services .card #card-icon-02 {
  background-color: #27ae60;
}
.section-two .services .card #card-icon-03 {
  background-color: #eb5757;
}
.section-two .services .card h3 {
  text-align: left;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: bolder;
  font-size: 2.4rem;
  line-height: 3.6rem;
  color: #333333;
}
.section-two .services .card .card-desc {
  text-align: left;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 2.4rem;
  color: #4f4f4f;
}
.section-two .services .card .card-button {
  width: fit-content;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  padding: 0.9rem 1.3rem;
  background-color: #2d9cdb;
  border: none;
  border-radius: 1.2rem;
  cursor: pointer;
}
@media (min-width: 992px) {
  .section-two .services .card .card-button {
    color: #828282;
    background-color: #e0e0e0;
  }
}
@media (min-width: 992px) {
  .section-two .services .card:hover {
    box-shadow: 0px 10px 30px rgba(51, 51, 51, 0.1);
    border-radius: 2.4rem;
  }
  .section-two .services .card:hover .card-button {
    color: #ffffff;
    background-color: #2d9cdb;
  }
}
@media (min-width: 768px) {
  .section-two .services .card:not(:first-child):not(:last-child) {
    margin: 3rem 3rem;
  }
}

/*************** section three ***************/
.section-three {
  margin: 4.3rem 1rem 4.3rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1200px) {
  .section-three {
    margin: 4.3rem 16.5rem 4.3rem 16.5rem;
    align-items: flex-start;
  }
}
.section-three h2 {
  margin: 4.8rem 3.5rem;
  width: 26.7rem;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 2.4rem;
  line-height: 3.6rem;
  color: #333333;
}
@media (min-width: 992px) {
  .section-three h2 {
    width: 44.8rem;
    font-size: 3.6rem;
    line-height: 5.4rem;
  }
}
.section-three .projects {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1200px) {
  .section-three .projects {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    column-gap: 2rem;
    row-gap: 2rem;
  }
}
.section-three .projects .card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 3.6rem;
}
@media (min-width: 1200px) {
  .section-three .projects .card {
    margin-bottom: 1rem;
  }
}
.section-three .projects .card picture > * {
  border-radius: 2.4rem;
}
.section-three .projects .card .desc {
  font-family: "Poppins", sans-serif;
  font-style: normal;
